.project-box {
    flex: 0 0 auto;
    width: 320px;
    height: 270px;
    padding: 20px;
    margin: auto;
    border: 1px solid #8f8e8e;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    text-align: center;
    position: relative;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.9;
    display: inline-block;
    transition: transform 0.3s ease-in-out;
}

/* .project-box:hover {
    transform: scale(1.02);
} */
.project-box h3 {
    margin-bottom: 15px;
    font-size: 1.5em;
    position: relative;
    border-radius: 5px;
    display: inline-block;
    padding: 5px;
    z-index: 2; 
    color: #fff;
}

.project-box p {
    margin-bottom: 10px;
    font-size: 1.1em;
    color: #fff;
    position: relative;

}

.project-box a {
    display: inline-block;
    margin-top: 10px;
    padding: 8px 12px;
    color: #fff;
    background-color: #007bff;
    border-radius: 4px;
    text-decoration: none;
    position: relative;
    z-index: 2; 
}

.project-box a:hover {
    background-color: #0056b3;
    cursor: pointer;
    transform: scale(1.05);
}

/* Overlay styles */
.overlay {
    background-color: gray;
    width: 100%;
    height: 85%;
    border-radius: 15px;
    opacity: 0.98;
    cursor: default;
}

.overlay:hover {
    opacity: 0;
    transition: opacity 0.5s ease;
}

@media (max-width: 600px) {
    .project-box {
        width: 320px;
        height: 270px;
        padding: 20px;
        margin: 20px;
        border: 1px solid #ddd;
        border-radius: 15px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
        text-align: center;
        position: relative;
        overflow: hidden;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        opacity: 0.9; 
    }
  }