.project-wrapper {

    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 30px;
    padding: 30px;
    padding-top: 50px;
    background-color: #f5f5f5;
    position: relative;
}
/*                  LOADING              */
#loading {
    display: none;
    position: absolute; 
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none; 
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.7);
    font-size: 2rem;
    color: #333;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    z-index: -10;
    opacity: 0;
    transition: opacity 0.3s ease;
  }

#loading.show {
    display: flex;
    opacity: 1;
    z-index: 1000;
}



@media (max-width: 600px) {
    .project-wrapper {
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
        justify-content: space-around;
        padding: 20px;
        padding-top: 30px;
        background-color: #f5f5f5;
    }
