.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #333;
    padding: 10px;
    font-size: 2rem;
    color: #fff;
    overflow-x: auto;
    white-space: nowrap;
    
  }

  .App-logo:hover, .lang:hover, .mobile-hide:hover{
    transform: scale(1.1);
    transition: transform 0.3s ease;
    cursor:pointer;
  }

.lang, .mobile-hide {
    display: inline-block;
    width: 90px;
    height: 90px ;
  }

  .App-logo{
    display: inline-block;
    width: 70px;
    height: 70px ;
    /* overflow-y: hidden; */
  }

  @media (max-width: 600px) {
    .navbar {
      padding: 30px; 
      font-size: 3rem;
      height: 100px;
    }
    .mobile-hide{
      display: none;
    }
  
    .App-logo, .lang {
      width: 80px !important;
      height: 80px !important;
      padding: 10px;
      
    }
  }

  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }